// React
import { Text, Box, Image } from '@chakra-ui/react';
// Library
import { getSprite } from '../../library/fetchProduct';


const ColorChoice = (props:any) => {
  const colors = Object.keys(props.choices);
  let selectedColor = props.state[0];

  const ColorRow = () => colors.map((color) => <Box
    p="3px"
    m="0 6px 0 0"
    h="43px"
    w="43px"
    border={ selectedColor === color ? "1px solid black" : "1px solid transparent" }
  >
    <Image
      src={getSprite(props.choices[color].swatchImage.filePath)}
      alt={props.choices[color].name}
      onClick={() => {props.state[1](color);}}
      cursor="pointer"
      border="1px solid #d0d0d0"
    />
  </Box>)

  return <Box w="100%">
    <Text
      display="flex"
      fontFamily="AvenirHeavy"
      fontWeight="500"
      fontSize="14px"
      p="24px 0 12px 0"
    >
      <b>COLOR:</b>&nbsp;
      <Text
        fontFamily="AvenirMedium"
        fontWeight="400"
        fontSize="14px"
      >{props.choices[selectedColor].name}</Text>
    </Text>
    <Box display="flex">
      {ColorRow()}
    </Box>
  </Box>
}


export default ColorChoice;
