// React
import { Box } from '@chakra-ui/react';
// Components
import OneColor from './oneColor';
import ColorChoice from './colorChoice';
import OneSize from './oneSize';
import SizeChoice from './sizeChoice';


const ProductOptions = (props:any) => {
  // Color details
  const selectedColorID = props.colorState[0];
  const colorCount = Object.keys(props.colors).length;
  const selectedColor = props.colors[selectedColorID];
  // Size details
  const sizeCount = props.sizes.orderedSizesBySeqNumber.length;
  // Side by Side options toggle
  const forceSideBySideOptions = sizeCount >= 6 && colorCount >= 6;

  const ColorOption = () => {
    if (colorCount === 1) {
      return <OneColor color={selectedColor}/>
    }
    return <ColorChoice
      default={selectedColorID}
      choices={props.colors}
      state={props.colorState}
    />
  }

  const SizeOption = () => {
    if (sizeCount === 1)
      return <OneSize size={props.sizes.sizeMap[props.sizes.orderedSizesBySeqNumber[0]].displayName}/>
    return <SizeChoice
      choices={props.sizes}
      state={props.sizeState}
      isAvailable={props.isAvailable}
    />
  }

  return <Box h="fit-content" marginTop="auto" marginBottom="-30px">
    <Box marginTop="auto" marginBottom="40px" flexDirection={forceSideBySideOptions ? "row" : "column"}>
      <ColorOption/>
      <SizeOption/>
    </Box>
  </Box>
}


export default ProductOptions;
