// React
import { useState } from 'react';
import { Text, Box } from "@chakra-ui/react";
// Assets
import LeftArrow from '../assets/svgs/leftArrow';


const ProductHeader = (props:any) => {
  const [isReadMore, setReadMore] = useState(false);
  const bulletText = props.bulletText || []
  const dimensionsBulletText = props.dimensionsBulletText || []

  return <>
    { isReadMore ?
        <Box
          zIndex="9"
          className="details-view"
          position="absolute"
          w="100%"
          h="100%"
          top="0"
          right="0"
          padding="64px 140px 0 140px"
          borderRadius="0px"
          backgroundColor="white"
        >
          <Text
            fontSize="14px"
            textDecoration="underline"
            cursor="pointer"
            onClick={() => setReadMore(!isReadMore)}
          >
            <LeftArrow width="10px" height="10px" marginRight="14px"/>
            Back
          </Text>
          <Text
            margin="12px 0 0 0"
            fontFamily="AvenirMedium"
            fontStyle="normal"
            fontSize="22px"
            fontWeight="500"
            letterSpacing="1px"
            paddingBottom="40px"
          >{ props.name }</Text>
          <Text
            margin="0 0 16px 0"
            fontFamily="Avenir"
            fontStyle="normal"
            fontSize="16px"
            fontWeight="600"
            letterSpacing="1px"
            paddingBottom="16px"
          >150TH BLOOMINGDALE'S CELEBRATION</Text>
          <Text
            fontFamily="Avenir"
            fontSize="14px"
            fontWeight="400"
            letterSpacing="1px"
            lineHeight="18px"
            padding="unset"
            margin="unset"
          >{props.description}
            <Text
              margin="32px 0 16px 0"
              fontFamily="Avenir"
              fontStyle="normal"
              fontSize="16px"
              fontWeight="600"
              letterSpacing="1px"
              paddingBottom="16px"
            >PRODUCT DETAILS</Text>
            <ul style={{ margin: '16px 0 32px 16px', fontFamily: 'Avenir', fontWeight: '400', fontSize: '14px' }}>
            {
              bulletText.map(
                (point:string) => <li key={point}>{point}</li>
              )
            }
            {
              dimensionsBulletText.map(
                (point:string) => <li key={point}>{point}</li>
              )
            }
            </ul>
            <a
              href={props.productURL}
              target="_blank"
              rel="noreferrer"
              style={{ textDecoration: 'underline', fontFamily: 'Avenir', fontSize: '14px', fontWeight: '400' }}
            >
              View Full Product Details
            </a>
            &nbsp;for more information regarding sizing, true fit, shipping and more.
          </Text>
        </Box>
      :
        <></>
    }
    <Box>

      <Text
        className="brand-name"
        textTransform="uppercase"
        fontFamily="AvenirHeavy"
        fontWeight="900"
        fontSize="20px"
        letterSpacing="4%"
        lineHeight="32.78px"
      >{ props.brand }</Text>

      <Text
        fontFamily="AvenirMedium"
        fontSize="18px"
        fontWeight="500"
        letterSpacing="4%"
        paddingBottom="16px"
      >{ props.name }</Text>

      { props.description.length > 0 ?
        <>
          <Text
            fontFamily="Avenir"
            fontSize="14px"
            fontWeight="400"
            letterSpacing="1px"
            lineHeight="18px"
            noOfLines={3}
          >{ props.description }</Text>
          <Text
            cursor="pointer"
            userSelect="none"
            fontFamily="Avenir"
            fontSize="14px"
            fontWeight="400"
            letterSpacing="1px"
            textDecoration="underline"
            padding="8px 0 32px 0"
            width="fit-content"
            height="fit-content"
            onClick={ () => setReadMore(!isReadMore) }
          >See More</Text>
        </>
        :
        <Text
          cursor="pointer"
          userSelect="none"
          fontFamily="Avenir"
          fontSize="14px"
          fontWeight="400"
          letterSpacing="1px"
          textDecoration="underline"
          padding="8px 0 32px 0"
          width="fit-content"
          height="fit-content"
          onClick={ () => setReadMore(!isReadMore) }
        >View Detail</Text>
      }
    </Box>
  </>
}


export default ProductHeader;
