// React
import { Box, Text } from '@chakra-ui/react';
// Assets
import WarningIcon from '../assets/svgs/warningIcon';


const ProductValue = (props:any) => <>
  <Text
    className="product-value"
    zIndex="2"
    display="flex"
    flexDirection="row"
    width="fit-content"
    height="fit-content"
    fontFamily="Avenir"
    fontSize="20px"
    fontWeight="600"
    lineHeight="20px"
    letterSpacing="0.04em"
    textTransform="capitalize"
    paddingTop="40px"
  >{
    props.originalPrice === props.currentPrice ?
      <>{props.currentPrice}</>
    :
      <>
        <Text color="#DE3412" m="0 12px 0 0">{props.currentPrice}</Text>
        <Text textDecoration="line-through">{props.originalPrice}</Text>
      </>
  }
  </Text>
  { props.showWarning ?
    <Box
      display={ "flex" }
      justifyContent="left"
      w="100%"
      h="50px"
      m="12px 0 0 0"
      color="red"
      border="1px solid #DE3412"
    >
      <WarningIcon width="18px" height="18px" margin="auto 0 auto 19px"/>
      <Text margin="auto 15px">Please select a size.</Text>
    </Box> : <></>
  }
</>


export default ProductValue;
