import { serverAPI } from '../shared/library/server/api';


export const fetchProduct = async (id:number, BAD:any = null, OK:any = null) => {
  /*
  The fetchProduct function is an async fetch wrapper which allows us to call the product api using a specific
  product id.

  We can also pass a "OK" callback which will run only if the response is considered "OK".
  We can also pass a "BAD" callback which will run only if the response is considered "BAD".

  Either the OK or BAD callbacks can be null, in which case we'll just return the product data.
  */
  const apiURL = `https://oweneaster.pythonanywhere.com/api/`;
  const apiURI = `digital/v1/product/${id}`
  const apiArgs = `clientId=PROS&_regionCode=US&currencyCode=USD&_shoppingMode=SITE&size=small&_customerState=GUEST`;
  const apiPath = `${apiURL}${apiURI}?${apiArgs}`;

  await fetch(apiPath)
  .then(resp => resp.json())
  .then(respJson => {

    // Parse API Response
    const respStatus = respJson.product !== undefined ? 'OK' : 'BAD';
    const respData = respJson.product[0];

    // OK API RESULT HANDLER
    if (respStatus === 'OK') {
      try { return OK !== null ? OK(respData) : respData }
      catch (error) {
        console.log(`OK Callback Error @ ${apiPath}`)
        console.log({status:respStatus, data:respData, error:error})
      }
    }

    // BAD API RESULT HANDLER
    else if (respStatus === 'BAD') {
      try{ return BAD !== null ? BAD(respJson) : respJson }
      catch (error) {
        console.log(`BAD Callback Error @ ${apiPath}`)
        console.log({status:respStatus, data:respData, error:error})
      }
    }
  })
}


export const getSprite = (filePath:string) => {
  const apiURL = `https://images.bloomingdalesassets.com/is/image/BLM/products/`;
  const apiURI = filePath;
  const apiArgs = `op_sharpen=1&wid=35&fit=fit,1&$filtersm$&fmt=webp`;
  return `${apiURL}${apiURI}?${apiArgs}`;
}


export default fetchProduct;
