// React
import {
  Modal, ModalOverlay, ModalContent, ModalBody, Box
} from "@chakra-ui/react";
import { useState, useEffect } from 'react';
// Assets
import ExampleBackgroundImg from './assets/images/example.png';
// Components
import AddToBasketBtn from './components/addToBasketBtn';
import CheckoutView from './components/checkoutView';
import CloseButton from './components/closeButton';
import ProductHeader from './components/productHeader';
import ProductImage from './components/productImage';
import ProductValue from './components/productValue';
import ProductOptions from './components/productOptions/productOptions';
// Library
import fetchProduct from './library/fetchProduct';

const ProductData = {
  id: 0,
  identifier: {
    productUrl: "/shop/product/product_name?ID=0",
  },
  detail: {
    name: "",
    description: "",
    brand: {
      name: ""
    },
    flags: { hasColors: false },
    numberOfColors: 0,
    typeName: "",
    bulletText: ["", ],
    dimensionsBulletText: ["", ]
  },
  imagery: {
    images: [
      {
        filePath: ""
      }
    ]
  },
  relationships: {
    taxonomy: {},
    upcs: {}
  },
  traits: {
    colors: {
      selectedColor: 0,
      colorMap: {
        "0": {
          id: 0,
          name: "",
          normalName: "",
          swatchImage: {
            filePath: "",
            name: "",
            showJumboSwatch: false,
            swatchSpriteOffset: 0,
            swatchSpriteUrlIndex: 0
          },
          imagery: {
            images: [
              {
                "filePath": "0/optimized/0_fpx.tif",
                "name": "0.fpx",
                "showJumboSwatch": false,
                "swatchSpriteOffset": 0,
                "swatchSpriteUrlIndex": 0
              },
            ]
          }
        }
      }
    },
    sizes: {
      orderedSizesBySeqNumber: [0, ],
      sizeMap: {
        "0": {
          id: 0,
          name: "",
          displayName: "",
          colors: [
            0
          ]
        },
      }
    },
  },
  pricing: {
    price: {
      tieredPrice: [
        { label: "", values: [{"value": 0, "formattedValue": "$0.00", "type": "regular"}] }
      ]
    }
  },
  urlTemplate: {
    swatch: "https://images.bloomingdalesassets.com/is/image/BLM/swatches/",
    swatchSprite: "https://images.bloomingdalesassets.com/is/image/BLM/",
    product: "https://images.bloomingdalesassets.com/is/image/BLM/products/"
  }
}


function ProductModal(props:any) {
  const [ product, setProduct ] = useState<any>(ProductData);
  const [ selectedColor, setColor ] = useState<any>(ProductData.traits.colors.selectedColor.toString());
  const [ selectedSize, setSize ] = useState<any>(undefined);
  const [ showCheckoutView, setCheckoutView ] = useState(false);
  const [ showWarning, setWarning ] = useState(false);
  const [ anyAvailability, setAvailability ] = useState(false);

  if (selectedSize !== undefined && showWarning) { setWarning(false); }
  let tieredPrices = product.pricing.price.tieredPrice.length;
  let originalPrice = product.pricing.price.tieredPrice[0].values[0].formattedValue;
  let currentPrice = product.pricing.price.tieredPrice[tieredPrices - 1].values[0].formattedValue;
  let availability = product.relationships.upcs;

  const isAvailable = (sizeID:any) => {
    /*
      Check if the given sizeID is in-stock when paired with the selectedColorID
    */
    let available = false;
    Object.keys(availability).forEach((upcs) => {
      if (availability[upcs].traits.colors.selectedColor === parseInt(selectedColor) &&
          availability[upcs].traits.sizes.selectedSize === parseInt(sizeID)) {
        available = availability[upcs].availability.available;
        if (available && !anyAvailability) setAvailability(true);
        return available
      }
    })
    return available
  }

  const getUPCS = () => {
    /*
      Check if the upcs codes and match one to the selectedColor & selectedSize
    */
    let _upcID = undefined;
    let _upcNumber = undefined;
    Object.keys(availability).forEach((upcs) => {
      if (availability[upcs].traits.colors.selectedColor === parseInt(selectedColor) &&
          availability[upcs].traits.sizes.selectedSize === parseInt(selectedSize)) {
        _upcID = upcs;
        _upcNumber = availability[upcs].identifier.upcNumber;
        return [ _upcID, _upcNumber ];
      }
    })
    return [ _upcID, _upcNumber ];
  }

  const updateProduct = (productID:number) => {
    /*
      Updates the product data to be displayed within the modal
    */
    fetchProduct(
      productID,
      null,
      (resp:any) => {
        setSize(undefined);
        setProduct(resp);
        setCheckoutView(false);
        setColor(resp.traits.colors.selectedColor.toString());
      }
    );
  }

  useEffect( () => {
    if (props.id !== 0) updateProduct(props.id);
  }, [props.id])

  // Render the constructed modal for this specific product
  if (product.id === 0) return <></>
  return <div className="main">
    <Modal onClose={props.onClose} isOpen={props.isOpen} isCentered size="6xl">
      <ModalOverlay
        backgroundColor="transparent"
        filter="blur(12px)"
        overflowY="hidden"
        w="100vw"
        h="100vh"
        backgroundImage={ExampleBackgroundImg}
      />
      <ModalContent className="modal-container" borderRadius="0px">
        <CloseButton onClose={props.onClose}/>

        <ModalBody p="0px" h="100%" w="100%" overflowY="hidden" overflowX="hidden">
          <Box className="modal-content" display="flex" h="inherit" w="inherit">
            <ProductImage
              url={product.urlTemplate.product}
              imageList={
                product.traits.colors.colorMap[selectedColor] !== undefined ?
                  product.traits.colors.colorMap[selectedColor].imagery.images
                  :
                  product.imagery.images
              }
            />{ showCheckoutView ? <CheckoutView
              name={product.detail.name}
              brand={product.detail.brand.name}
              onClose={props.onClose}
            /> : <>
            <Box
              className="product-details"
              display="flex"
              flexDirection="column"
              justifyContent="space-between"
              w="50%"
              maxHeight="calc(100% - 130px)"
            >
              <ProductHeader
                name={product.detail.name}
                brand={product.detail.brand.name}
                productURL={"https://www.bloomingdales.com" + product.identifier.productUrl}
                description={product.detail.description}
                bulletText={product.detail.bulletText}
                dimensionsBulletText={product.detail.dimensionsBulletText}
              />
              <ProductOptions
                colors={product.traits.colors.colorMap}
                colorState={[ selectedColor, setColor ]}
                sizes={product.traits.sizes}
                sizeState={[ selectedSize, setSize ]}
                isAvailable={isAvailable}
              />
              <ProductValue originalPrice={originalPrice} currentPrice={currentPrice} showWarning={showWarning}/>
            </Box>
            <AddToBasketBtn
              color={selectedColor}
              size={selectedSize}
              showWarning={[ showWarning, setWarning ]}
              state={[ showCheckoutView, setCheckoutView ]}
              anyAvailability={anyAvailability}
              getUPCS={getUPCS}
            />
          </>}</Box>
        </ModalBody>
      </ModalContent>
    </Modal>
  </div >
}


export default ProductModal;
