// React
import { useState } from 'react';
import { Box, Image } from '@chakra-ui/react';
// Assets
import LeftArrow from '../assets/svgs/leftArrow';
import RightArrow from '../assets/svgs/rightArrow';
import ExpandPhoto from '../assets/svgs/expandPhoto';
import MenuClose from '../assets/svgs/MenuClose';


const ProductImage = (props:any) => {
  const [isEnlarged, setEnlarged] = useState(false);
  const [imageIndex, setImageIndex] = useState(0);
  const imageCount = props.imageList.length;
  const imagePath = () => {
    if (imageIndex > imageCount - 1) {
      setImageIndex(0);
      return props.url + props.imageList[0].filePath;
    }
    return props.url + props.imageList[imageIndex].filePath;
  }

  const PhotoLeftBtn = (props:any) => {
    return <Box
      className={props.className}
      position={props.position}
      left={props.left}
      bottom={props.bottom}
      w="40px"
      h="40px"
      p="10px"
      backgroundColor="rgba(255,255,255,0.8)"
      onClick={ () => {
        if (imageIndex > 0) setImageIndex(imageIndex - 1);
      }}
    ><LeftArrow width="100%" height="100%"/></Box>
  }

  const PhotoRightBtn = (props:any) => {
    return <Box
      className={props.className}
      position={props.position}
      right={props.right}
      bottom={props.bottom}
      w="40px"
      h="40px"
      p="10px"
      backgroundColor="rgba(255,255,255,0.8)"
      onClick={ () => {
        if (imageIndex < imageCount - 1) setImageIndex(imageIndex + 1);
      }}
    ><RightArrow width="100%" height="100%"/></Box>
  }

  return <>
    {
      isEnlarged ?
      <Box position="fixed" top="0" left="0" zIndex="99" w="100%" h="100%" overflow="auto">
        <Box
          className="clickable"
          position="fixed"
          top="10px"
          right="20px"
          p="10px 10px 10px 10px"
          backgroundColor="rgba(255,255,255,0.8)"
          onClick={ () => setEnlarged(!isEnlarged) }
        ><MenuClose width="20px" height="20px"/></Box>
        <Image  // This image uses a different "wid" value of 1200
          src={imagePath() + '?op_sharpen=1&wid=1200&fit=fit,1&$filtersm$&fmt=webp'}
          alt="product preview"
          w="100%"
        />
        <PhotoLeftBtn className="clickable" position="fixed" left="0%" bottom="50%"/>
        <PhotoRightBtn className="clickable" position="fixed" right="5px" bottom="50%"/>
      </Box> : <></>
    }
    <Box
      zIndex="1"
      className="showClickableOnHover image-container"
      bg="white"
      overflowY="hidden"
      boxShadow={"" /*"0px 0px 20px 10px rgba(0, 0, 0, 0.05)"  <--- the sexiest accident since crocs */}
    >
      <Image  // This image requires a smaller "wid" value of 720
        src={imagePath() + '?op_sharpen=1&wid=720&fit=fit,1&$filtersm$&fmt=webp'}
        alt="product preview"
        className="product-image"
        objectFit="cover"
      />
      <PhotoLeftBtn className="hidden-clickable image-left-button" position="absolute"/>
      <PhotoRightBtn className="hidden-clickable image-right-button" position="absolute"/>
      {/* PHOTO ENLARGEMENT */}
      <Box
        className="clickable image-expand-button"
        position="absolute"
        w="40px"
        h="40px"
        p="10px"
        backgroundColor="rgba(255,255,255,0.8)"
        onClick={ () => setEnlarged(!isEnlarged) }
      >
        <ExpandPhoto
          width="100%"
          height="100%"
        />
      </Box>
    </Box>
  </>
}


export default ProductImage;
