// React
import { Box, Button, Text } from '@chakra-ui/react';


const AddToBasketBtn = (props:any) => {
  return <Box
    className="add-to-bag-button"
    position="absolute"
    right="0"
    bottom="0"
    w="50%"
    zIndex="0"
    padding="32px 64px 64px 64px"
    background="transparent"
  >
    <Button
      id="add_to_cart"
      position="relative"
      bg="black"
      w="100%"
      h="50px"
      padding="auto 20px"
      borderRadius="0px"
      onClick={() => {

        if (props.size === undefined) {
          props.showWarning[1](true)
        } else {
          props.state[1](true)
        }

        const [ upcID, upcNumber ] = props.getUPCS();
        const message = {
          data: {
            event_id: 'add_to_cart',
            quantity: 1,
            source: 'PDPA2B',
            trackingCategory: 89,
            upcId: upcID,
            upcNumber: upcNumber,
            loyaltyPoints: true,
            pickUpFromStore: "false"
          }
        }

        console.log(`Sending ${message.data.event_id} to parent window...`)
        console.log(message)
        window.parent.postMessage(message, '*');

      }}
      backgroundColor={props.anyAvailability ? "black" : "#949494"}
    >
      <Text
        color="white"
        margin="2px 0 0 0"
        textTransform="uppercase"
        fontFamily="AvenirMedium"
        fontStyle="normal"
        fontWeight="500"
        lineHeight="18px"
        letterSpacing="0.04em"
      >Add To Bag</Text>
    </Button>
  </Box>
}


export default AddToBasketBtn;
