// React
import { Text, Box, Select } from '@chakra-ui/react';


const SizeChoice = (props:any) => {
  const sizes = props.choices.orderedSizesBySeqNumber;
  const sizeMap = props.choices.sizeMap;

  const SizeRow = () => {
    return sizes.map((size:number) => {
      const sizeAvailable = props.isAvailable(size);
      let color = size === props.state[0] ? '#fff' : '#000';
      let backgroundColor = size === props.state[0] ? '#000' : 'transparent';
      let onClick = () => props.state[1](size);

      if (!sizeAvailable) {
        color = '#767676';
        backgroundColor = '#e5e5e5';
        onClick = () => null;
        if (props.state[0] === size) {props.state[1](undefined)}
      }

      return <Box
        position="relative"
        marginLeft={size === sizes[0] ? 'unset !important' : undefined}
        backgroundColor={backgroundColor}
        onClick={onClick}
      >
        {sizeAvailable ? <></> : <Box className="out-of-stock"/>}
        <Text
          position="absolute"
          color={color}
          textAlign="center"
          padding="2px"
          backgroundColor={sizeAvailable ? "transparent" : backgroundColor}
        >
          {sizeMap[size].displayName}
        </Text>
      </Box>
    })
  }

  const SizeList = () => {
    return sizes.map((size:number) => {
      const sizeAvailable = props.isAvailable(size);
      return <option
        value={size}
        disabled={!sizeAvailable}
        style={{ color: sizeAvailable ? "black" : "#D6D6D6", textDecoration: 'line-through' }}
      >
        {sizeMap[size].displayName}
      </option>
    })
  }

  return <>
    <Box display="flex" w="100%" p="24px 0 12px 0">
      <Text
        fontFamily="AvenirHeavy"
        fontWeight="500"
        fontSize="14px"
      >
        SIZE:&nbsp;
      </Text>
      <Text
        fontFamily="AvenirMedium"
        fontWeight="400"
        fontSize="14px"
      >
        Please select
      </Text>
    </Box>
    { sizes.length <= 6 ?
        <Box className="size-select-boxes">
          {SizeRow()}
        </Box>
      :
      <Select
        value={props.state[0]}
        onChange={(event) => props.state[1](event.target.value)}
        placeholder='Select Size' size='lg' borderRadius="0px" height="60px"
        color="white" m="0px 0px" borderColor="gray" textColor="gray"
        _focus={{ borderColor: "gray" }}
        _hover={{ borderColor: "gray" }}
        _active={{ borderColor: "gray" }}
        _focusVisible={{ borderColor: "gray", boxShadow: "0 0 0 1px black" }}
      >
        {SizeList()}
      </Select>
    }
  </>
}


export default SizeChoice;
